@font-face {
  font-family: 'UbuntuRegular';
  src: url('../fonts/Ubuntu-Regular.eot');
  src: url('../fonts/Ubuntu-Regular.woff2') format('woff2'), url('../fonts/Ubuntu-Regular.woff') format('woff'), url('../fonts/Ubuntu-Regular.ttf') format('truetype'), url('../fonts/Ubuntu-Regular.svg#Ubuntu-Regular') format('svg'), url('../fonts/Ubuntu-Regular.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'UbuntuBold';
  src: url('../fonts/Ubuntu-Bold.eot');
  src: url('../fonts/Ubuntu-Bold.woff2') format('woff2'), url('../fonts/Ubuntu-Bold.woff') format('woff'), url('../fonts/Ubuntu-Bold.ttf') format('truetype'), url('../fonts/Ubuntu-Bold.svg#Ubuntu-Bold') format('svg'), url('../fonts/Ubuntu-Bold.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'UbuntuMedium';
  src: url('../fonts/Ubuntu-Medium.eot');
  src: url('../fonts/Ubuntu-Medium.woff2') format('woff2'), url('../fonts/Ubuntu-Medium.woff') format('woff'), url('../fonts/Ubuntu-Medium.ttf') format('truetype'), url('../fonts/Ubuntu-Medium.svg#Ubuntu-Medium') format('svg'), url('../fonts/Ubuntu-Medium.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}

.orange {
  background-color: #FF5700 !important;
}

body {
  font-family: "UbuntuMedium";
  overflow-x: hidden;
  color: #404040;
}

body a {
  font-family: "UbuntuMedium";
  text-decoration: none;
  color: #000;
}

input::-webkit-input-placeholder {
  color: #8E8E8E;
  font-family: "UbuntuMedium";
  font-size: 14px;
}

input:-moz-placeholder {
  color: #8E8E8E;
  font-family: "UbuntuMedium";
  font-size: 14px;
}

button,
button:active,
button:focus {
  outline: none !important;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.top_bar {
  width: 100%;
  padding: 20px 0px;
  background-color: #f5f5f5;
  -webkit-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.5);
  z-index: 50;
  position: fixed;
  top: 0px;
}

.top_bar .wrapper {
  max-width: 1170px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.top_bar hgroup h4 {
  color: #e8e809;
  font-family: "UbuntuBold";
  font-size: 14px;
  padding-left: 5px;
}

.top_bar hgroup h2 {
  font-family: "UbuntuBold";
  font-size: 30px;
  line-height: 20px;
  color: #019448;
}

.my_map {
  margin-top: 76px;
  width: 100vw;
  height: calc(100vh - 76px);
  z-index: 5;
}

.button_open_instruct {
  position: fixed;
  bottom: 20px;
  right: calc((100% - 200px)/2);
  width: 200px;
  height: 50px;
  background-color: #f5f5f5;
  border: 1px solid #eee;
  -webkit-box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: all linear 0.2s;
}

.button_open_instruct:hover {
  transform: scale(1.05);
}

.modal_container {
  display: none;
  position: fixed;
  z-index: 100;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.35);
}

.modal_container .container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 50px;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.modal_container .container .modal {
  cursor: default;
  position: relative;
  padding: 40px;
  background-color: #fff;
  text-align: center;
  box-sizing: border-box;
  min-width: 30%;
  margin-bottom: -100%;
  -webkit-box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 5px 20px 5px rgba(0, 0, 0, 0.5);
  transition: all ease-out 0.5s;
}

.modal_container .container .modal.show_modal {
  margin-bottom: 50px;
}

.modal_container .container .modal .item {
  margin-top: 20px;
}

.modal_container .container .modal .item:first-child {
  margin-top: 0px;
}

.modal_container .container .modal select {
  margin-top: 15px;
  width: 250px;
  height: 40px;
}

.modal_container .container .modal h2 {
  font-size: 1.5em;
  margin-bottom: 15px;
}

.modal_container .container .modal p {
  text-align: center;
}

@media screen and (max-width: 960px) {
  .top_bar .wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 780px) {
  .top_bar .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .top_bar .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }

  .modal_container .container .modal {
    min-width: 90%;
    max-width: 90%;
    padding: 20px;
  }
}

@media screen and (max-width: 420px) {
  .top_bar .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}